<template>
	<div style="position: relative;height: 100%;">
		<div class="web">
			<div>
				<h1></h1>
			</div>
			<div id="captcha">
				<p id="wait" class="show">......</p>
			</div>
		</div>
		<div class="ICP">
			<a href="https://beian.miit.gov.cn/" target="_blank">ICP2023015234</a>
		</div>
	</div>
</template>
<script>
	import "../lib/gt.js"
	import "../lib/jquery.js"
	import "../lib/gt4.js"
	import {
		getHeaders
	} from "../tool/ds.js"
	import md5 from "md5"

	export default {
		name: "web",
		mounted() {
			let param = this.$route.query
			if (param.use_v4) {
				if (!param.gt || !param.risk_type || !param.mmt_key) {
					$("#wait")[0].innerText = ""
					return
				}
			} else {
				if (!param.gt || !param.challenge || !param.success || !param.new_captcha) {
					$("#wait")[0].innerText = ""
					return
				}
			}

			function cb(captchaObj) {
				captchaObj.appendTo("#captcha")
				captchaObj.onReady(function() {
					$("#wait").hide();
					param.use_v4 ? captchaObj.showCaptcha() : captchaObj.verify()
				});
				captchaObj.onSuccess(function() {
					let result = captchaObj.getValidate()
					let h = param.use_v4 ? md5(`mmt_key=${param.mmt_key}&gt=${param.gt}`) : md5(
						`challenge=${param.challenge}&gt=${param.gt}`)
					$.ajax({
						url: "/result/success",
						type: "post",
						dataType: "json",
						headers: {
							"Content-Type": "application/json;charset=utf-8;"
						},
						data: JSON.stringify({
							key: h,
							data: result
						}),
						success: function(data) {
							if (data.code == 0) alert("")
							else alert("")
						},
						error() {
							alert("")
						}
					})
				})
			}

			let p = param.use_v4 ? {
				captchaId: param.gt,
				riskType: param.risk_type,
				language: "zho",
				hideSuccess: false,
				product: "bind",
				userInfo: JSON.stringify({
					mmt_key: param.mmt_key
				}),
				onError: function(err) {
					$("#wait")[0].innerText = `reason${err.message||"unknown"}`
				}
			} : {
				gt: param.gt,
				challenge: param.challenge,
				offline: !param.success,
				new_captcha: param.new_captcha,
				onError: function(err) {
					$("#wait")[0].innerText = `reason${err.message||"unknown"}`
				}
			}

			if (param.use_v4) {
				initGeetest4(p, cb)
			} else {
				initGeetest(p, cb)
			}
		}
	}
</script>

<style>
	#captcha {
		display: flex;
		justify-content: center;
	}

	div {
		text-align: center;
	}

	.ICP {
		position: absolute;
		bottom: 10px;
		right: 10px;
	}

	.web h1 {
		margin-top: 0;
		padding-top: 0.67em;
	}
</style>